.itemsizeContainer{
    display: flex;

}


.itemSize {
    width: 26px;
    height: 26px;
    margin:0 10px;
    padding-left:2px;
    background-color: #ececec;
    color: #3f3a3a;
    border-radius: 50%;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}


.itemSize[aria-pressed=true]{
    background: #0d0d0d;
    border-width: 6px;
    color: #fff;
}

.detailPic{
    height: 30rem;
    float:right;
    margin:0 2rem;
}

.countBar{
    width: 5rem;
    height: 1rem;
    background: #3f3a3a;
}

.detailText{
    font-size: 1rem;
    letter-spacing: 4px;
    text-align: left;
    color: #3f3a3a;
    padding:0.5rem 0 0 0 ;
}


.detailColor{
    width: 1.2rem;
    height: 1.2rem;
    padding: 0.5rem 0rem;
    margin: 0 1rem;
    display:inline-block;
}


.cartBtn{
    width: 16rem;
    height: 3rem;
    padding: 0.4rem;
    margin:2rem 0;
    background-color: black;
    color:#fff;
}

.amountBtn{
    display: inline-flex;
    width: 6rem;
    height: 2rem;
    border: solid 1px gray;
    margin:0 1rem;
    padding:0.2rem 1rem;
    flex-direction: row;
    justify-content: space-between;
}



.profileContainer{
    width:auto;
    margin:50px 100px;
}



.profileTextContainer{
    text-align: center;
}

.profileText{
    font-size: 2rem;
    margin: 0 10px 30px 0;
    display: inline-block;
}


.logContent{
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0 0 10px 0 ;
}

.logText{
  margin:10px 10px 10px 0;
  font-size: 1.3rem;
}

.logInput{
  width: 30%;
  height: 30px;
  border: solid 1px #979797;
  border-radius: 8px;
}

.logBtnContsainer{
    display:flex;
    justify-content: center;
}

.logBtn{
    width:80px;
    height:40px;
    margin:30px 0;
    background-color: black;
    color:aliceblue;
    font-size:16px;
}
*{
    box-sizing: border-box;
    margin:0;
}


a{
    text-decoration: none;
    color:black;
}
